import $ from 'jquery';
import { Countdown } from '../objects/Countdown';

const SELECTOR_CONTAINER = '[data-countdown-banner]';
const SELECTOR_COUNTDOWN = '[data-countdown-container]';

class CountdownBanner {
  constructor(container) {
    this.container = container;

    //Countdown
    let countdownContainer = container.find(SELECTOR_COUNTDOWN);

    if (countdownContainer) {
      this.countdown = new Countdown(countdownContainer);
    }
  }
}

/*** Initialize  ***/
$(document).ready(() => {
  //Find container
  let container = $('[data-countdown-banner]');
  if(!container.length) return;
  if(container.attr('data-initialized')) return;

  //Create the template, we use window.collection for TESTING only!!
  let countdownBanner = new CountdownBanner(container);

});