/*
 *  Countdown
 *    Scripts for controlling countdown.
 *    Will init on all [data-countdown-container]'s by default, but can be supered as well
 *
 *  Version:
 *    1.0.0 - 2021/10/26
 */
import $ from 'jquery';

const SELECTOR_ACTIVE = '[data-countdown-active]';
const SELECTOR_COMPLETE = '[data-countdown-complete]';
const SELECTOR_COUNTDOWN = '[data-countdown]';
const SELECTOR_DAYS = '[data-days]';
const SELECTOR_HOURS = '[data-hours]';
const SELECTOR_MINUTES = '[data-minutes]';
const SELECTOR_SECONDS = '[data-seconds]';

const ATTR_YEAR = 'data-target-year';
const ATTR_MONTH = 'data-target-month';
const ATTR_DAY = 'data-target-day';
const ATTR_HOUR = 'data-target-hour';
const ATTR_MIN = 'data-target-minute';

const CLASS_HIDDEN = 'is-hidden';

export class Countdown {
  constructor(container) {
    this.container = container;
    this.countdown = container.find(SELECTOR_COUNTDOWN);

    //setup data attributes
    let targetYear = parseInt(this.countdown.attr(`${ATTR_YEAR}`));
    let targetMonth = parseInt(this.countdown.attr(`${ATTR_MONTH}`));
    let targetDay = parseInt(this.countdown.attr(`${ATTR_DAY}`));
    let targetHour = parseInt(this.countdown.attr(`${ATTR_HOUR}`));
    let targetMinute = parseInt(this.countdown.attr(`${ATTR_MIN}`));
    let targetDate = new Date(targetYear, targetMonth - 1, targetDay, targetHour, targetMinute);

    this.initializeClock(targetDate);
  }

  getTimeRemaining(endtime) {
    const total = Date.parse(endtime) - Date.parse(new Date());
    const seconds = Math.floor((total / 1000) % 60);
    const minutes = Math.floor((total / 1000 / 60) % 60);
    const hours = Math.floor((total / (1000 * 60 * 60)) % 24);
    const days = Math.floor(total / (1000 * 60 * 60 * 24));

    return {
      total,
      days,
      hours,
      minutes,
      seconds
    };
  }

  initializeClock(endtime) {
    if (!this.container) return;
    const clock = this.container.find(SELECTOR_ACTIVE);
    const daysSpan = this.container.find(SELECTOR_DAYS);
    const hoursSpan = this.container.find(SELECTOR_HOURS);
    const minutesSpan = this.container.find(SELECTOR_MINUTES);
    const secondsSpan = this.container.find(SELECTOR_SECONDS);
    const countdownComplete = this.container.find(SELECTOR_COMPLETE);
  
    const updateClock = (interval)  => {
      const t = this.getTimeRemaining(endtime);
  
      // daysSpan.html(t.days);

      t.days === 0 ? daysSpan.html('00') : daysSpan.html(('0' + t.days).slice(-2));
      t.hours === 0 ? hoursSpan.html('00') : hoursSpan.html(('0' + t.hours).slice(-2));
      t.minutes === 0 ? minutesSpan.html('00') : minutesSpan.html(('0' + t.minutes).slice(-2));
      t.seconds === 0 ? secondsSpan.html('00') : secondsSpan.html(('0' + t.seconds).slice(-2));
  
      if (t.total <= 0) {
        if (interval !== undefined) {
          clearInterval(interval);
        }
        hideCountdown();
      }
    }
  
    const hideCountdown = () => {
      clock.addClass(CLASS_HIDDEN);
      countdownComplete.removeClass(CLASS_HIDDEN);
    }
    // to display counter faster without setInterfal for the first time
    updateClock(0);
    let timeinterval = setInterval(function () {
      updateClock(timeinterval);
    }, 1000);
  }
}
